import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: "/",
      redirect: "/home"
    },
    {
      path: '/home',
      component: function (resolve) {
        require(["@/views/home/index.vue"], resolve);
      },
    },
    {
      path: '/about',
      component: function (resolve) {
        require(["@/views/about/index.vue"], resolve);
      },
    },
    {
      path: '/xxxx',
      component: function (resolve) {
        require(["@/views/HelloWorld.vue"], resolve);
      },
    },
  ]
})



