/**
 * dd api
 */

import {
    service
} from "../utils/request.js";
/**
 * code 获取 token
 */
export function getDdToken(param) {
    return service({
        url: `/weixin/officialAccount/getOpenId`,
        method: 'post',
        data: {
            ...param
        }
    })
}



