const config = {
  corpId: 'ding5d79a48772bb61fe35c2f4657eb6378f',
}
let bindToGlobal = (obj, key) => {
  if (typeof window[key] === 'undefined') {
      window[key] = {};
  }
  for (let i in obj) {
      window[key][i] = obj[i]
  }
}
bindToGlobal(config, '_config')


