

import dd from "dingtalk-jsapi";
import {
  getDdToken
} from "../api/ddLogin.js";
/* code 授权  start */
export function getDdCode() {
  return new Promise((resolve, reject) => {
    if (dd.env.platform != 'notInDingTalk') {
      dd.ready(function () {
        dd.runtime.permission.requestAuthCode({
          corpId: _config.corpId,
          onSuccess: function (result) {
            getDdToken({
              code: result.code
            }).then(res => {
              if (res.code == 200 && res.data) {
                localStorage.setItem("access_token", res.data.token);
                resolve({
                  code: 200,
                  data: '成功！'
                })
              } else {
                reject({
                  code: "404",
                  message: "异常错误!"
                })
              }
            }).catch(error => {
              reject({
                code: "404",
                message: "异常错误!"
              })
            })

          },
          onFail: function (err) {
            reject({
              code: "404",
              message: "异常错误!"
            })
          }
        });
      });
      dd.error((error) => {
        reject({
          code: "404",
          message: "异常错误!"
        })
      });
    } else {
      reject({
        code: "404",
        message: "不在钉钉内！"
      })
    }

  })
}
/* code 授权  end */


